.feedback-form {
  max-width: 480px;
  background-color: #b7ebea;
  border: 1px solid rgba(119, 108, 213, .12);
  border-radius: 10px;
  flex-direction: column;
  padding: 12px;
  font-weight: 500;
  display: flex;
}

.feedback-form label {
  color: #4d59a6;
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  color: #4d59a6;
  background-color: #a6dee6;
  border: 1px solid #09858e;
  border-radius: 12px;
  padding: 8px 12px;
  font-size: larger;
  font-weight: 700;
  line-height: 1.5;
}

input, textarea {
  font: inherit;
  background-color: #d6f2f6;
  border: 1px solid #09858e;
  border-radius: 12px;
  padding: 8px;
}

textarea {
  resize: none;
  background-color: #d6f2f6;
}

.feedback-form button:hover {
  color: #4d59a6;
  letter-spacing: 2px;
  background-color: #9acdd4;
  font-weight: 700;
  box-shadow: 4px 4px 3px #47666d;
}

.link {
  cursor: pointer;
  width: fit-content;
  color: #3030c1;
  background-color: #97a5ff;
  border-radius: 120px 100px 15px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 500;
  transition: background-color .3s ease-in-out;
  box-shadow: 0 2px 4px 2px rgba(143, 209, 233, .9);
}

.link:hover {
  background: #82d8e5 linear-gradient(#0147a6, #fff49f);
  transition: background-color 2s linear;
  box-shadow: 4px 4px 0 1px rgba(56, 100, 105, .5);
}

